	import {mapState, mapGetters} from 'vuex';
    import uMask from "../u-mask/u-mask.vue";
	
    export default {
        name: "app-close",
        components:{
            uMask
        },
	    props: {
            modal: {
                type: Boolean,
                default() {
                    return true;
                }
            },
            toBack: {
                type: Boolean,
                default() {
                    return false;
                }
            },
            mch_id: {
                type: [Number, String],
                default() {
                    return 0;
                }
            },
            mch_list: {
                type: String,
                default() {
                    return ''
                }
            }
	    },
        data() {
            return {
                mallStatus: {
                    is_open: 0,
                    auto_open_text: ''
                },
                isMall: true,
                list: ''
            }
        },
        created() {
            let para = {};
            let mch_list = ''
            if(this.mch_list) {
                mch_list = this.mch_list;
                if(mch_list.length > 0) {
                    para.mch_id_list = mch_list;
                    if(mch_list.indexOf(0) == -1) {
                        this.isMall = false;
                    }
                }
            }
            if(this.mch_id > 0) {
                para.mch_id_list = JSON.stringify([this.mch_id]);
                this.isMall = false;
            }
            this.$request({
                url: this.$api.index.status,
                data: para
            }).then(response => {
                console.log(para)
                let mallStatus = response.data;
                if(!this.isMall) {
                    response.data.shift();
                }
                this.mallStatus = response.data[0]
                if(this.mch_id > 0) {
                    for(let item of response.data) {
                        if(item.mch_id == this.mch_id) {
                            this.mallStatus = item
                        }
                    }
                }
                if(mch_list.length > 0) {
                    this.list = '';
                    for(let item of response.data) {
                        if(item.is_open == 2) {
                            if(!this.mallStatus.auto_open_text) {
                                this.mallStatus.auto_open_text = item.auto_open_text;
                            }
                            this.mallStatus.is_open = item.is_open;
                            if(this.list.length > 0) {
                                this.list += '、'
                            }
                            this.list += item.name
                        }
                    }
                    this.$emit('update', this.mallStatus)
                }else {
                    this.$emit('update', this.mallStatus)
                }
            })
        },
	    computed: {
            ...mapGetters('mallConfig', {
                getTheme: 'getTheme',
            }),
            ...mapState({
                mall: state => state.mallConfig.mall,
                userInfo: state => state.user.info,
            })
	    },
        methods: {
            toIndex() {
                if(this.toBack) {
                    uni.navigateBack();
                }else {
                    uni.redirectTo({
                        url: '/pages/index/index'
                    })
                }
            },
        }
    }
